import React, { useState } from 'react';
import imagenUas1 from '../../img/uas1.jpg'
import imagenUas2 from '../../img/uas2.jpg'
import imagenUas3 from '../../img/uas3.jpg'

import "./Inicio.scss"
import MostrarServicios from './MostarServicios';
import Horarios from './Horiarios';
import Ubicacion from './Ubicacion';
import { useNavigate } from "react-router-dom"


const Inicio = () => {
  const navigate = useNavigate();
  return (

    <div className='inicio'>
      <div className='layout_inicio'>
        <div className='titulo'>
          <h1>OH MY NAILS</h1>
        </div>
        <div className='separador_color'>
          <h1>OH MY NAILS</h1>
          <p>Bienvenida a nuestro sitio web de reservas!</p>
          <p>Nosotras te brindaremos diseos nicos para tus uas.</p>
          <div className='reservar'>
            <button className='btn btn-reservar' onClick={() => { navigate('/inicio/realizar_reserva') }}>
              Realizar Reserva
            </button>
          </div>
        </div>
        <img className="img1" src={imagenUas1} alt='imagen'></img>
        <div className='reservar'>
          <button className='btn btn-reservar' onClick={() => { navigate('/inicio/realizar_reserva') }}>
            Realizar Reserva
          </button>
        </div>
      </div>
      <MostrarServicios></MostrarServicios>
      <div className="main-container">
        <Horarios />
        <Ubicacion />
      </div>
    </div>

  );
};

export default Inicio;
